@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --background: #f4eae6;
  --primary: #e57f84;
  --primary-light: #f7d4d4;
  --secondary: #1e405c;
  --tertiary: #679186;
  --tertiary-light: #bbd4ce;
}

.App {
  text-align: center;
  font-family: "Quicksand", sans-serif;
  color: #1e405c;
  color: var(--secondary);
  background-color: #f4eae6;
  background-color: var(--background);
}

.general-info {
  padding: 5% 8%;
}

.photo {
  width: 50%;
  border-radius: 50%;
  border: 2px solid #1e405c;
  border: 2px solid var(--secondary);
}

.name {
  font-size: 2em;
  margin-bottom: 0.25em;
}

.heading {
  font-size: 2em;
  font-weight: bold;
  padding: 0 1em;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.squeeze {
  padding: 0 2em;
}

.text {
  font-size: larger;
}

.justify {
  text-align: justify;
  text-justify: inter-word;
}

.section {
  margin-top: 5em;
  padding: 0 3em;
}

.bubble {
  display: inline-flex;
  align-items: center;
  color: #f4eae6;
  color: var(--background);
  border-radius: 20px;
  padding: 0.5em 1em;
  margin: 0.3em;
}

@media only screen and (max-width: 600px) {
  .photo {
    width: 100%;
  }

  .section {
    padding: 1em;
  }

  .heading {
    font-size: 1em;
  }
}

.title,
.modal-title {
  font-size: 2em;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
}

.subtitle,
.modal-subtitle {
  font-size: 1.2em;
  margin-bottom: 1.5em;
  font-family: "Quicksand", sans-serif;
}

.subtitle, .title {
  color: var(--secondary);
}

.date {
  font-family: "Quicksand", sans-serif;
}

.logo {
  width: 100%;
}

.modal {
  margin: 10% auto;
  padding: 2em 3em 3em 3em;
  width: 50%;
  background-color: var(--tertiary-light);
  color: var(--tertiary);
  outline: 0;
  border-radius: 5px;
}

.close-btn-container {
  text-align: right;
}

.close-btn {
  cursor: pointer;
}

.go-to {
  color: var(--secondary);
}

.project-title {
  font-size: 1.4em;
  font-weight: bold;
  color: var(--secondary);
}

.project-date {
  margin: 0.2em;
  font-size: 0.9em;
  color: var(--secondary);
}

.project-description {
  margin-top: 1em;
  text-align: justify;
  text-justify: space-between;
  color: var(--secondary);
}

.skills {
    background-color: var(--tertiary-light);
    color: var(--tertiary);
    padding: 3em;
    margin-top: 5em;
}

.contact-div {
    margin: 3em 0;
    background-color:var(--primary-light);
    padding: 3em 2em;
}

.contact-heading {
    color: var(--primary);
}

.welcome-text {
    font-family: 'Quicksand', cursive;
    font-size: 7em;
    margin: 0;
    width: 80%;
}

.welcome-div {
    height: 100vh;
    padding: 0 12%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
}

.circle {
    width: 27vh;
    height: 27vh;
    border-radius: 50%;
    display: block;
    margin: auto;
}

.bubble-div {
    width: 20%;
    float: center;
}
